@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');

body {
  margin: 0px;
  width: 100vw;
  height: 100vh;
  font-family: 'Inter', sans-serif;
}
.headerSection {
  display: flex;
  height: 62vh;
  width: 100vw;
  background: #ffcd9e;
}

.title {
  width: 40vw;
  height: 12vh;
  margin-left: 9.5vw;
  margin-top: 8.4vh;
  margin-bottom: 0vh;
}

.content {
  width: 41.25vw;
  height: 8.1vh;
  margin-left: 9.5vw;
  margin-top: 0vh;
  margin-bottom: 0vh;
}

.sub-content {
  width: 18.91vw;
  height: 6vh;
  margin-left: 9.5vw;
  margin-top: 0vh;
  margin-bottom: 0vh;
}

.input {
  height: 8vh;
  width: 20.83vw;
  margin-left: 9.3vw;
  border-radius: 0.66vw;
  padding-left: 1.83vw;
  font-weight: 40;
  font-size: 1.66vw;
  line-height: 160%;
  align-items: center;
}

.btn {
  width: 10.83vw;
  height: 8.9vh;
  margin-left: 1.33vw;
  border-radius: 0.66vw;
  background: #282828;
  color: #ffffff;
  font-weight: 700;
  font-size: 2vh;
  line-height: 2.4vh;
  align-items: center;
}

.sub-sub-content {
  text-align: center;
  width: 28.66vw;
  height: 6.1vh;
  margin-left: 11vw;
  color: #ffffff;
}

.dweep-image {
  width: 34vw;
  height: 54vh;
  margin-top: 1.5vh;
  margin-left: 18vh;
  margin-right: 4.7vh;
}

.form {
  display: flex;
}

.footerSection {
  display: flex;
  height: 38vh;
  width: 100vw;
  background: #282828;
}

.ft-left-content {
  color: #ffffff;
  width: 18vw;
  height: 3.6vh;
  margin-left: 19.1vh;
  margin-bottom: 0vh;
  margin-top: 0vh;
}

.first {
  margin-top: 6.2vh;
}

.ft-right-content {
  color: #ffffff;
  width: 12.33vw;
  height: 3.6vh;
  margin-left: 84.5vh;
  text-align: right;
  margin-bottom: 0.2vh;
}

.first-right {
  margin-top: 6.2vh;
  height: 2.8vh;
  margin-left: 84vh;
}

.icons {
  margin-top: 1.2vh;
}